









import Vue from 'vue';
import {Component, Prop, Watch} from 'vue-property-decorator';

@Component
export default class Types extends Vue {
    @Prop() value!:string
    selectType(type:string){
        if(type !== '-' && type !== '+'){
            throw new Error('type is unknown')
        }
        this.$emit('update:value',type)
    }
}
