

































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import recordMixin from '@/mixins/recordMixin';
import Chart from '@/components/Chart.vue';
import _ from 'lodash';


@Component({
    components: {Tabs, Chart}
})
export default class Statistics extends Vue {
    yyy = '-';
    types = recordMixin;


    mounted() {
        const div = (this.$refs.chartWrapper as HTMLDivElement);
        div.scrollLeft = div.scrollWidth;
    }

    tagString(tags: Tag[]) {
        return tags.length === 0 ? '无' : tags.map(t => t.name).join('，');
    }

    meihua(string: string) {
        const day = dayjs(string);
        if (day.isSame(dayjs(), 'day')) {
            return '今天';
        } else if (day.isSame(dayjs().subtract(1, 'day'), 'day')) {
            return '昨天';
        } else if (day.isSame(dayjs().subtract(2, 'day'), 'day')) {
            return '前天';
        } else if (day.isSame(dayjs(), 'month')) {
            return day.format('M月-DD日');
        } else {
            day.format('YYYY年-MM月-DD日');
        }
    }

    beforeCreate() {
        this.$store.commit('fetchRecord');
    }

    get recordList() {
        return (this.$store.state as RootStore).recordList;
    }

    get keyValueList() {
        const today = new Date();
        const array = [];
        for(let i=0;i<=29;i++){
            const date = dayjs(today).subtract(i,'day').format('YYYY-MM-DD')
            const found =  _.find(this.recordGroup,{title:date})
            array.push({date:date,value:found?found.total : 0});
        }
        console.log(array);
        array.sort((a,b)=>{
            if(a.date > b.date){
                return 1
            }else if(a.date === b.date){
                return 0
            }else {
                return -1
            }
        })
        return array
    }

    get chartOptions() {

        const keys = this.keyValueList.map(d => d.date);
        const values = this.keyValueList.map(v => v.value);
        return {
            grid: {left: 0, right: 10},
            xAxis: {
                axisLine: {
                    lineStyle: {color: '#666'}
                },
                type: 'category',
                data: keys,
                axisTick: {alignWithLabel: true},
                axisLabel: {
                    formatter: function (value: string, index: number) {
                        return value.substr(6);
                    }
                }

            },
            yAxis: {
                type: 'value',
                shadow: false,
            },
            series: [{
                data: values,
                type: 'line',
                symbolSize: 12,
                itemStyle: {
                    color: '#666',
                },
                symbol: 'circle'
            }],
            tooltip: {show: true}
        };
    }

    get recordGroup() {
        const {recordList} = this;
        const newList = clone(recordList).filter(t => t.type === this.yyy).sort((a, b) => dayjs(b.createAl).valueOf() - dayjs(a.createAl).valueOf());
        if (newList.length === 0) {
            return [];
        }

        type Group = { title: string, total?: number, items: RecordItem[] }[]
        const group: Group = [{title: dayjs(newList[0].createAl).format('YYYY-MM-DD'), items: [newList[0]]}];
        for (let i = 1; i < newList.length; i++) {
            const current = newList[i];
            const last = group[group.length - 1];
            if (dayjs(last.title).isSame(dayjs(current.createAl), 'day')) {
                last.items.push(current);
            } else {
                group.push({title: dayjs(current.createAl).format('YYYY-MM-DD'), items: [current]});
            }
        }
        group.map(group => {
            group.total = group.items.reduce((sum, item) => sum + item.amount
                , 0);
        });
        return group;
    }
}
